:root{
  font-size: 16px;
}

/* Media */

@media (min-width: 150px) {
  :root{
    font-size: 5px;
  }
}
@media (min-width: 780px) {
  :root{
    font-size: 9px;
  }
}
@media (min-width: 990px) {
  :root{
    font-size: 12px;
  }
}
@media (min-width: 1200px) {
  :root{
    font-size: 16px;
  }
}  

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  background-color:  #09092F !important;
}

.App {
  text-align: center;
  background-color: #09092F;
  color: #ccc;
  font-size: 1rem;
}

.container {
  padding: 0 1rem;
}

.page-banner {
  padding: 5rem 0 2.5rem 0;
  margin: 1.5rem 0 1.5rem 0;
  position: relative;
  color: #fff;
  opacity: 1;
  text-shadow: 2px 2px #0b4cb5;
}
.picture-banner {
  opacity: 0.80;
  background-size: cover;
  background-image: url('./image/background.jpg');
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.page-banner img{
  width:8.75rem;
  height: 7.5rem;
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s ease;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

.page-banner h2{
  font-size: 3rem;
  padding-top: 30px;
  float: left;
}
.page-banner h3{
  font-size: 1rem;
}
.page-banner h4{
  font-size: 0.10rem;
}

.gallery > img {
  max-width:100%; 
  height:12.5rem;
  width: auto;
  padding: 0.3rem 0.3rem 1.5rem 0.3rem;
  margin: 0.3rem 0.3rem 0.6rem 0.3rem;
  align-items: center;
  box-sizing: border-box;
  background-color: white;
  box-shadow: 0 0.2rem 0.4rem 0 #ccc, 0 0.3rem 0.7rem 0 #333;
}



.link-style {
  color: #fff;
  text-decoration: none;
}

.link-style:hover {
  color:#048004;
  text-decoration: none;
}

::placeholder {
  color:#ff0000;
}

.btn-green {
  border: none;
  background: #666;
  color: #fff;
  padding: 5px 10px;
  cursor: pointer;
  float: right;
}

.btn-green:hover {
  background: #048004;
}

.page-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2px;
  background-color: #f0f0f0;
}
@media (max-width: 500px) {
  .page-list {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 0px;
  }
}

.page-item {
  padding: 10px; 
  border:  1px #333 dotted;
  color: #333;
}

.btn-red {
  border: none;
  background: #ff0000;
  color: #ccc;
  padding: 5px 10px;
  cursor: pointer;
  float: right;
  border-radius: 50%;
}

.footer {
  padding: 10px 0 10px 0;
  margin: 10px 0 10px 0;
  position: relative;
  color: #ccc;
  font-size: xx-small;
}

.hidden {
  display: none;
}

li {
  list-style: none;
}

li a {
  display: block;
  text-align: center;
  color: #ccc;
  font-size: 1.25rem;
}
li a:hover {
  opacity: 0.75;
  color:#048004;
}